<style lang="less">
.pt-nav {
  width: 100%;
  .ul {
    width: 100%;
    .ulchind {
      width: 100%;
      text-align: left;
      text-indent: 1em;
      .ultxt {
        display: block;
        height: 39px;
        width: 100%;
        line-height: 39px;
        background: #eef9ff;
        color: #666;
        font-size: 18px;
        cursor: default;
        font-family: "宋体";
        i {
          color: #1D8CE0;
        }
      }
      .ol {
        width: 100%;
        display: none;
        .olchild {
          width: 100%;
          height: 35px;
          line-height: 35px;
          text-align: left;
          text-indent: 3em;
          cursor: default;
          font-size: 16px;
          background: rgb(216, 241, 255);
          color: #666;
          &:hover{
            background: #7eb3ea;
            color: #fdf8f8;
          }
        }
        .navact{
          background: #7eb3ea;
          color: #fdf8f8;
        }
      }
    }
  }
}
</style>
<template>
  <div class="pt-nav">
    <ul class="ul">
      <li class="ulchind" v-for="(item,index) in navdata">
        <span class="ultxt"><i class="el-icon-menu" :style="{'margin-right':'5px'}"></i>{{item.name}}</span>
        <ol class="ol">
          <router-link activeClass="navact" v-for="list in item.child" :key="list" class="olchild" :to="list.router" tag="li">{{list.text}}</router-link>
        </ol>
      </li>
    </ul>
    <!--router-link中使用:key是因为不使用会有webpack热加载报一个警告。在:to里面需要用到list调用里面的值。在这里是没有申明的。直接使用会报警告。用:key申明一下list可以使用就不报警告了-->
  </div>
</template>
<script>
  import $ from 'jquery'
  export default {
    name: 'pt-nav',
    data(){
      return {

      }
    },
    props: ['navdata'],
    mounted(){
      $(".ul li span").click(function () {
        $('.ul li .ol').slideUp();
        $(this).siblings('.ol').slideDown();
      })
      var spanel = $(".navact").parent(".ol").siblings("span").length;//判断是否存在已经选中状态的.navact类名
      if(spanel){
        $(".navact").parent(".ol").siblings("span").click();
      }else {
        $(".ul li span:eq(0)").click();
        $(".ul li ol:eq(0) li:eq(0)").click();
      }
    },
    methods: {

    }
  }
</script>







