<style lang="less">
.login {
  width: 100%;
  height: 100%;
  /*background: #2c3e50;*/
  position: relative;
  .login-info {
    width: 500px;
    height: 300px;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    background: #fff;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    padding: 50px;
    box-shadow: 0 0 0px 0px #ece9e9,0 0 20px 0px #f3e5e5;
  }
}
.login-info {
  .toIndex {
    position: absolute;
    top: -32px;
    right: 0;
    width: 95px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    background: forestgreen;
    color: #fff;
    &:hover {
      background: darkgreen;
    }
  }
  input {
    width: 100%;
    height: 39px;
    border: 1px solid #ddd;
    margin-top: 25px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    padding: 0 30px;
    border-radius: 17px;
    font-size: 14px;
  }
  input[type="button"] {
    background: forestgreen;
    color: #fff;
    &:hover {
      background: darkgreen;
    }
  }
}
</style>
<template>
  <div class="login">
      <div class="login-info">
        <button class="toIndex" @click="toIndex">首页</button>
        <input type="text" placeholder="请输入账号" v-model="user.userName">
        <input type="password" placeholder="请输入密码" v-model="user.userpwd" @keyup.enter="submit">
        <input type="button" value="登录" @click="submit" @keyup.enter="submit">
      </div>
  </div>
</template>
<script>
  export default {
    name: 'login',
    data(){
        return {
            user: {
                userName: '',
                userpwd: ''
            }
        }
    },
    methods: {
      submit(){
          var userName = this.user.userName;
          if(userName == 'hospital'){
            this.$router.push({path: '/hospitalMenu'});
          }else if(userName == 'expert'){
            this.$router.push({path: '/expertMenu'});
          }else if(userName == 'allocating'){
            this.$router.push({path: '/allocatingMenu'});
          }else if(userName == 'manage'){
            this.$router.push({path: '/manageMenu'});
          }else if(userName == 'outlay'){
            this.$router.push({path: '/outlayMenu'});
          }else if(userName == 'release'){
            this.$router.push({path: '/releaseMenu'});
          }else {
            alert("用户名或密码错误")
          }
      },
      toIndex(){
          this.$router.push({path: '/'});
      }
    }
  }
</script>



