// 医院端：hospital
// 专家端：expert
// 分配端：allocating
// 发布端：release
// 管理端：manage
// 计费端：outlay
module.exports = {
  hospital: [
    {
      name: '常规病历',
      child: [
        {text: '新建病历', router: '/hospital/addRecord'},
        {text: '待诊病历', router: '/hospital/pendingRecord'},
        {text: '已诊病历', router: '/hospital/alreadyRecord'},
        {text: '退回病历', router: '/hospital/retreatRecord'}
      ]
    },
    {
      name: '会诊病历',
      child: [
        {text:'新建会诊',router: '/hospital/addConsult'},
        {text:'处理中会诊',router: '/hospital/InProcessConsult'},
        {text:'已处理会诊',router: '/hospital/alreadyConsult'}
      ]
    },
    {
      name: '冰冻',
      child: [
        {text:'待预约冰冻',router: '/hospital/pendingYYFrozen'},
        {text:'待处理冰冻',router: '/hospital/pendingCLFrozen'},
        {text:'已处理冰冻',router: '/hospital/alreadyHandle'}
      ]
    },
    {
      name: '统计查询',
      child: [
        {text:'统计',router: '/hospital/statistics'},
        {text:'查询',router: '/hospital/query'}
      ]
    },
    {
      name: '模板维护',
      child: [
        {text:'模板维护',router: '/hospital/templateMaintain'}
      ]
    }
  ],
  expert:[
    {
      name: '日程安排',
      child: [
        {text: '日程安排', router: '/expert/schedule'}
      ]
    },
    {
      name: '常规病历',
      child: [
        {text: '待诊病历', router: '/expert/stayDiagnosis'},
        {text: '已诊病历', router: '/expert/alreadyDiagnosis'}
      ]
    },
    {
      name: '会诊病历',
      child: [
        {text: '处理中', router: '/expert/beingProcessed'},
        {text: '已处理', router: '/expert/alreadyHandleConsult'}
      ]
    },
    {
      name: '冰冻病历',
      child: [
        {text: '冰冻预约', router: '/expert/frozenOrder'},
        {text: '待处理冰冻', router: '/expert/stayHandle'},
        {text: '已处理冰冻', router: '/expert/alreadyHandleFrozen'}
      ]
    },
    {
      name: '统计查询',
      child: [
        {text: '统计', router: '/expert/statistics'},
        {text: '查询', router: '/expert/query'}
      ]
    },
  ],
  allocating:[],
  release:[],
  manage:[],
  outlay:[],
};



