<style lang="less">
	.Index {
		width:  100%;
		height: 100%;
    font-family: "SimSun", "宋体";
    position: relative;
    .Index-info {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      margin: auto;
      width: 200px;
      height: 200px;
      h1 {
        font-weight: 600;
        color: #333;
        cursor: pointer;
      }
      button {
        width: 95px;
        height: 32px;
        margin-top: 12px;
        line-height: 32px;
        font-size: 16px;
        text-align: center;
        background: forestgreen;
        color: #fff;
        &:hover {
          background: darkgreen;
        }
      }
    }
	}
</style>
<template>
<div class="Index">
  <div class="Index-info">
    <h1 @click="toIndex">{{msg}}</h1>
    <button @click="toLogin">登录</button>
  </div>
</div>
</template>
<script>
export default {
  name: 'index',
  data () {
    return {
      msg: '人仁医'
    }
  },
  methods: {
    toLogin(){
        this.$router.push({path: '/login'});
    },
    toIndex(){
        this.$router.push({path: '/'});
    }
  }
}
</script>



