<style lang="less">
@import "../../css/layout";
</style>
<template>
  <div class="expert layout">
    <div class="layout-header">
      <h1 @click="toMenu">LOGO</h1>
      <div class="user">
        <div class="user-info">
          <el-dropdown trigger="click"  @command="handleCommand">
            <span class="el-dropdown-link">
              专家端<i class="el-icon-caret-bottom el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="账号设置">账号设置</el-dropdown-item>
              <el-dropdown-item command="修改密码">修改密码</el-dropdown-item>
              <el-dropdown-item command="退出登录">退出登录</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </div>
    <div class="layout-main">
      <div class="layout-main-nav">
        <pt-nav-view :navdata="navInfo"></pt-nav-view>
      </div>
      <div class="layout-main-info">
        <router-view></router-view>
      </div>
    </div>
    <div class="copyright">
      Copyright 2006-2017 91360.com All Rights Reserved.如有问题，请速与我们联系，电话：40004-91360
      <br>
      宁卫网审（2015）41号 | 苏ICP备13032250号 | 苏B2-20160359 |
      <img class="copyright-img" src="http://style.91360.com/Images/备案图标.png" alt="">
      苏公网安备 32011402010112号使用帮助
    </div>
  </div>
</template>
<script>
  import ptNavData from '../../api/pt-nav.js'
  import ptNavView from '../public/pt-nav.vue'
  export default {
    name: 'expert',
    data(){
      return {
        navInfo: ptNavData.expert,
      }
    },
    components: {
      ptNavView
    },
    methods: {
      toMenu(){
        this.$router.push({path: '/expertMenu'})
      },
      handleCommand(command) {
        if(command == "退出登录"){
          this.$router.push({path: '/'})
        }else {
          this.$message('点击了' + command + '没有其他的操作');
        }
      }
    }
  }
</script>



