/*
* 当前为配置路由文件---api下的pt-nav.js文件为执行分发路由文件
* */

import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index/index'
import login from '@/components/login/login'

Vue.use(Router)

export default new Router({
  routes: [
    {path: '/', component: index},
    {path: '/login', component: login},
    {path: '/hospitalMenu', component: require('@/components/menu/hospitalMenu')},
    {path: '/expertMenu', component: require('@/components/menu/expertMenu')},
    {path: '/allocatingMenu', component: require('@/components/menu/allocatingMenu')},
    {path: '/releaseMenu', component: require('@/components/menu/releaseMenu')},
    {path: '/manageMenu', component: require('@/components/menu/manageMenu')},
    {path: '/outlayMenu', component: require('@/components/menu/outlayMenu')},
    {
      path: '/hospital',//医院端
      component: require('@/components/hospital/hospital'),
      children: [
        {path: '/hospital',                   component: require('@/components/hospital/children/routine/addRecord')},           //常规病历
        {path: '/hospital/addRecord',         component: require('@/components/hospital/children/routine/addRecord')},           //常规病历---新建病历
        {path: '/hospital/pendingRecord',     component: require('@/components/hospital/children/routine/pendingRecord')},       //常规病历---待诊病历
        {path: '/hospital/alreadyRecord',     component: require('@/components/hospital/children/routine/alreadyRecord')},       //常规病历---已诊病历
        {path: '/hospital/retreatRecord',     component: require('@/components/hospital/children/routine/retreatRecord')},       //常规病历---退回病历

        {path: '/hospital/addConsult',        component: require('@/components/hospital/children/consult/addConsult')},          //会诊病历---新建会诊
        {path: '/hospital/InProcessConsult',  component: require('@/components/hospital/children/consult/InProcessConsult')},    //会诊病历---处理中会诊
        {path: '/hospital/alreadyConsult',    component: require('@/components/hospital/children/consult/alreadyConsult')},      //会诊病历---已处理会诊

        {path: '/hospital/pendingYYFrozen',   component: require('@/components/hospital/children/frozen/pendingYYFrozen')},      //冰冻病历---待预约冰冻
        {path: '/hospital/pendingCLFrozen',   component: require('@/components/hospital/children/frozen/pendingCLFrozen')},      //冰冻病历---待处理冰冻
        {path: '/hospital/alreadyHandle',     component: require('@/components/hospital/children/frozen/alreadyHandle')},        //冰冻病历---已处理冰冻

        {path: '/hospital/statistics',        component: require('@/components/hospital/children/statistics/statistics')},       //统计查询---统计
        {path: '/hospital/query',             component: require('@/components/hospital/children/statistics/query')},            //统计查询---查询

        {path: '/hospital/templateMaintain',  component: require('@/components/hospital/children/templateMaintain/templateMaintain')},//模板维护---模板维护
      ]
    },
    {
      path: '/expert',//专家端
      component: require('@/components/expert/expert'),
      children: [
        {path: '/expert',                    component: require('@/components/expert/children/schedule/schedule')},              //日程安排
        {path: '/expert/schedule',           component: require('@/components/expert/children/schedule/schedule')},              //日程安排--日程安排

        {path: '/expert/stayDiagnosis',      component: require('@/components/expert/children/routine/stayDiagnosis')},          //常规病历--待诊病历
        {path: '/expert/alreadyDiagnosis',   component: require('@/components/expert/children/routine/alreadyDiagnosis')},       //常规病历--已诊病历

        {path: '/expert/beingProcessed',     component: require('@/components/expert/children/consult/beingProcessed')},         //会诊病历--处理中
        {path: '/expert/alreadyHandleConsult',component: require('@/components/expert/children/consult/alreadyHandle')},          //会诊病历--已处理

        {path: '/expert/frozenOrder',        component: require('@/components/expert/children/frozen/frozenOrder')},             //冰冻病历--冰冻预约
        {path: '/expert/stayHandle',         component: require('@/components/expert/children/frozen/stayHandle')},              //冰冻病历---待处理冰冻
        {path: '/expert/alreadyHandleFrozen',component: require('@/components/expert/children/frozen/alreadyHandle')},           //冰冻病历---已处理冰冻

        {path: '/expert/statistics',         component: require('@/components/expert/children/statistics/statistics')},          //统计查询---统计
        {path: '/expert/query',              component: require('@/components/expert/children/statistics/query')},               //统计查询---查询
      ]
    },
    {
      path: '/allocating',//分配端
      component: require('@/components/allocating/allocating')
    },
    {
      path: '/release',//发布端
      component: require('@/components/release/release')
    },
    {
      path: '/manage',//管理端
      component: require('@/components/manage/manage')
    },
    {
      path: '/outlay',//计费端
      component: require('@/components/outlay/outlay')
    }
  ]
})



